.mainFooter{
	width: 100%;
	height: $tamanoFooter;
	@media #{$media-tablet}{
		padding: 0 50px;
	}
	&__content{
		height: 100%;
		@include flexbox;
		@include align-items(center);
		@include justify-content(space-between);
	}
	&--copy{
		p{
			color: $negro-texto;
			@include roboto-light;
			font-size: (13/16)+rem;
		}
	}
	&--social{
		display: block;
		transform: translateX(56px);
		@media #{$media-tablet}{
			transform: none;
		}
		img{
			width: 32px;
			transform: none;
		}
	}
}
