.lastestShows{
	height: 100%;
	overflow-y: auto;
	@media #{$media-tablet}{
		height: auto;
	}
	&__row{
		counter-increment: contadorRow;
		height: 100px;
		@include flexbox;
		@media #{$media-tablet}{
			height: auto;
			padding-bottom: 20px;
			-webkit-box-shadow: 0px 1px 0px 0px rgba($negro-texto, 0.5);
			-moz-box-shadow:    0px 1px 0px 0px rgba($negro-texto, 0.5);
			box-shadow:         0px 1px 0px 0px rgba($negro-texto, 0.5);
		}
		&:first-child{
			.lastestShows__row--left, .lastestShows__row--right{
				div[class *= "container"]{
					&::after{
						content: "";
						left: 0;
						top: -5px;
						width: 100%;
						height: 5px;
						position: absolute;
						background-color: rgba($negro-texto, 0.5);
						@media #{$media-tablet}{
							display: none;
						}
					}
				}
			}
		}
		&--left, &--right{
			height: 100%;
			&-container{
				height: 100%;
				padding-top: 25px;
				position: relative;
				-webkit-box-shadow: 0px 1px 0px 0px rgba($negro-texto, 0.5);
				-moz-box-shadow:    0px 1px 0px 0px rgba($negro-texto, 0.5);
				box-shadow:         0px 1px 0px 0px rgba($negro-texto, 0.5);
				@media #{$media-tablet}{
					box-shadow: none;
				}
			}
		}
		&--left{
			width: 165px;
			min-width: 165px;
			padding-right: 80px;
			@media #{$media-tablet}{
				width: 130px;
				min-width: auto;
				padding-right: 30px;
			}
			&-hour{
				color: $blanco;
				@include roboto-regular;
			}
			&-time{
				margin-top: 15px;
				padding-left: 20px;
				background-size: 10px;
				color: $negro-texto-2;
				@include roboto-light;
				font-size: (13/16)+rem;
				background-repeat: no-repeat;
				background-image: url('../img/icons/iconClock.svg');
			}
		}
		&--right{
			width: 100%;
			&-container{
				padding-left: 45px;
			}
			&-name{
				color: $blanco;
				@include roboto-regular;
				&:before{
					left: 0;
					position: absolute;
					color: $verde-active;
					@include roboto-bold;
					content: counter(contadorRow);
				}
			}
			&-by{
				margin-top: 15px;
				@include playfair;
				font-style: italic;
				color: $negro-texto-2;
				font-size: (13/16)+rem;
			}
		}
	}
}


.reproductor{
	width: 100%;
	height: $tamanoReproductor;
	@include flexbox;
	@media #{$media-tablet}{
		height: $tamanoReproductor*2;
		@include flex-wrap(wrap-reverse);
	}
	&__player{
		min-width: 40%;
		max-width: 40%;
		overflow: hidden;
		position: relative;
		@media #{$media-tablet}{
			min-width: 100%;
			max-width: 100%;
			height: $tamanoReproductor;
		}
		&--progress{
			left: 10%;
			width: 100%;
			height: 100%;
			margin-left: -100%;
			position: absolute;
			background-color: $negro;
			z-index: 1;
		}
		&--controls{
			z-index: 5;
			width: 100%;
			padding: 0 30px;
			position: relative;
			height: calc(100% - 2px);
			background-color: $gris-player;
			@include flexbox;
			&-play, &-volumen, &-info{
				top: 0;
				width: 35px;
				height: 100%;
				cursor: pointer;
				margin-left: 3px;
				margin-right: 3px;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: contain;
				@media #{$media-movil}{
	            	width: 50px;
					margin-left: 1px;
					margin-right: 1px;
	            }
			}
			&-play{
				&.play{background-image: url('../img/player/play.png');}
				&.pause{background-image: url('../img/player/pause.png');}
				&.stop{background-image: url('../img/player/stop.png');}
			}
			&-volumen{
				&.sound{background-image: url('../img/player/sound.png');}
				&.amplitude-muted{
					background-image: url('../img/player/mute.png');
				}
				input{
					right: 0;
					bottom: 0;
					z-index: 1;
					opacity: 0;
					width: 100%;
					position: absolute;
					@include transition(0.3);
				}
				&:hover{
					input{
						opacity: 1;
					}
				}
			}
			&-info{
				background-image: url('../img/player/info.png')
			}
			&-bar{
				margin-left: 20px;
				@include flexbox();
				@include align-items(center);
			}
		}
	}
	&__songs{
		max-width: 40%;
		min-width: 40%;
		overflow: hidden;
		position: relative;
		@media #{$media-tablet}{
			min-width: 100%;
			max-width: 100%;
			height: $tamanoReproductor;
		}
		&--controls{
			height: 100%;
			@include flexbox;
			@include justify-content(space-between);
			&-prev ,&-next{
				z-index: 1;
				width: 65px;
				height: 100%;
				display: block;
				cursor: pointer;
				position: relative;
				background-repeat: no-repeat;
				background-position: center center;
			}
			&-prev{background-image: url('../img/player/prev.png')}
			&-next{background-image: url('../img/player/next.png')}
		}
		&--name{
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			color: $negro-texto;
			@include roboto-light;
			font-size: (16/16)+rem;
			text-align: center;
			@include flexbox;
			@include align-items(center);
			@include justify-content(center);
		}
	}
}

.fullPageMarkers{
	z-index: 15;
	right: calc( (calc(100vw - 1366px)) / 2 );
	height: calc(100vh - #{$tamanoHeader*2});
	max-height: calc(800px - #{$tamanoHeader*2});
	position: fixed;
	top: 0;
	bottom: 0;
	margin-top: auto;
	margin-bottom: auto;
	width: $tamanoHeader;
	@include flexbox;
	@include align-items(center);
	@include justify-content(center);
	@media only screen and (max-width : 1366px) {
		right: 0;
	}
	@media #{$media-tablet}{
		display: none;
	}
	div{
		margin: 0;
		padding: 0;
		a{
			width: 20px;
			display: block;
			padding: 10px 0;
			cursor: pointer;
			list-style: none;
			position: relative;
			&::after{
				content: "";
				right: 0;
				top: 0;
				bottom: 0;
				width: 50%;
				height: 1px;
				display: block;
				margin-top: auto;
				margin-bottom: auto;
				position: absolute;
				background-color: $negro-bg;
				@include transition(0.3);
			}
			&.active{
				&::after{
					height: 2px;
					width: 100%;
					background-color: $verde-active;
				}
			}
		}
	}
}
