header{

}
.mainHeader{
	position: relative;
	height: $tamanoHeader;
	background-color: $blanco;
	@include flexbox;
	@include justify-content(flex-end);
	z-index: 100;
	@media #{$media-movil}{
		height: $tamanoHeaderMovil;
		background-color: $negro-bg;
	}
	.hamburger-menu{
		display: none;
		@media #{$media-movil}{
			display: block;
		}
	}
	&__nav{
		height: 100%;
		@include flexbox;
		@include align-items(center);
		&-open{
			@media #{$media-movil}{
				display: block!important;
			}
		}
		ul{
			margin: 0;
			padding: 0;
			li{
				margin: 0;
				padding: 0;
				width: 85px;
				margin: 0 5px;
				text-align: center;
				display: inline-block;
				a{
					padding: 0 4px;
					color: $negro-titulo;
					@include roboto-bold;
					font-size: (15/16)+rem;
					span{
						z-index: 1;
						position: relative;
					}
					&:before, &:after{
						bottom: 2px;
						height: 4px;
					}
				}
			}
		}
		@media #{$media-movil}{
			width: 100%;
			display: none;
			position: absolute;
			top: $tamanoHeaderMovil;
			background-color: $negro-bg;
			height: calc(100vh - #{$tamanoHeaderMovil});
			ul{
				li{
					display: block;
					width: 100%;
					a{
						font-size: (21/16)+rem;
						margin: 10px 0;
						color: $negro-texto;
					}
				}
			}
		}
	}
	&__traduction{
		a{
			display: block;
			text-align: center;
			width: $tamanoHeader;
			height: $tamanoHeader;
			@include roboto-black;
			@include flexbox;
			@include align-items(center);
			@include justify-content(center);
			font-size: (30/16)+rem;
			background-color: $negro-bg;
			@media #{$media-movil}{
				width: $tamanoHeaderMovil;
				height: $tamanoHeaderMovil;
			}
		}
	}
}
