
.contactForm{
	padding: 50px;
	input[type='text'],
	input[type='password'],
	input[type='date'],
	input[type='phone'],
	input[type='number'],
	input[type='email'],
	textarea{
		width: 100%;
		padding: 25px;
		outline: none;
		color: $blanco;
		border: 1px solid $blanco;
		background-color: transparent;
		@include roboto-light;
		font-size: (14/16)+rem;
		resize: none;
		@include placeholder{
			color: rgba($blanco, 0.7);
		}
	}
	textarea{
		height: 100px;
	}
	&__row{
		margin-bottom: 25px;
		&:last-child{
			margin-bottom: 0;
		}
	}
}
