// ↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓ _______ Start Video background _______ ↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
.video--bg{
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    z-index: 0;
    position: absolute;
    @media #{$media-movil}{
    	display: none;
    }
    video{
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }
}
