.home{
	// overflow: hidden;
	@include flexbox;
	@include align-items(center);
	@include justify-content(center);
	background-color: $negro-bg;
	background-image: url('../img/xxxxx.jpg');
	background-size: cover;
	&__logo{
		text-align: center;
		span{
			display: none;
		}
		img{
			width: 900px;
			@media #{$media-movil}{
				width: 165px;
				max-height: 80vh;
			}
		}
	}
	&__start{
		left: 0;
		width: 100%;
		bottom: 120px;
		text-align: center;
		position: absolute;
		@media #{$media-movil}{
			margin-top: 10px;
			position: static;
		}
		a{
			height: 27px;
			color: $blanco;
			font-size: (16/16)+rem;
			@include roboto-medium;
		}
	}
}


.containerMedia{
	.containerData__right{
		position: relative;
		background-size: cover;
		background-image: url('../img/media/videoPreview.jpg');
	}
	&--iconMega{
		z-index: 1;
		right: 67px;
		bottom: -50px;
		position: absolute;
		@media #{$media-movil}{
			display: none;
		}
	}
}

.containerEvents{
	.containerData__right{
		padding: 70px 90px 47px 82px;
		@media #{$media-tablet}{
			padding: 30px;
		}
	}
}

.containerGallery{
	.containerData__right{
		background-color: $blanco;
	}
	&__carrusel{
		height: 100%;
		@media #{$media-tablet}{
			height: auto;
		}
		.owl-stage-outer{
			height: 100%;
			.owl-stage{
				height: 100%;
				.owl-item{
					height: 100%;
					background-color: $negro-bg;
					.containerGallery__carrusel--item{
						height: 100%;
						overflow: hidden;
						img{
							width: auto;
							height: 100%;
							max-width: none;
							cursor: pointer;
							margin-left: -250%;
							@media #{$media-tablet}{
								width: 100%;
								margin-left: 0;
							}
						}
					}
				}
			}
		}
		.owl-nav{
			top: 0;
			bottom: 0;
			width: 100%;
			height: 20px;
			margin: auto;
			padding: 0 7px;
			position: absolute;
			.owl-prev, .owl-next{
				color: $verde-active;
			}
			.owl-prev{
				float: left;
			}
			.owl-next{
				float: right;
			}
		}
	}
}

.sectionMedia{
	&__carrusel{
		height: 100%;
		overflow: hidden;
		position: relative;
		.owl-stage-outer{
			height: 100%;
			.owl-stage{
				height: 100%;
				.owl-item{
					height: 100%;
				}
			}
		}
		&--item{
			height: 100%;
			background-size: cover;
			background-position: center center;
			img{
			}
		}
		.owl-nav{
			top: 0;
			bottom: 0;
			width: 100%;
			height: 0px;
			margin-top: auto;
			margin-bottom: auto;
			position: absolute;
			@include flexbox;
			@include justify-content(space-between);
			padding: 0 10px;
		}
	}
}

.owl-prev, .owl-next{
	img{
		@media #{$media-tablet}{
			width: 15px;
		}
	}
}
