$color_loading1 : #00ff00;
$color_loading2 : #40fc00;
$color_loading3 : #80f900;
$color_loading4 : #bff500;
$color_loading5 : #fff200;

.loading{
    width: 100%;
    height: 100%;
    z-index: 9999;
    position: fixed;
    background-color: $negro-bg;
    ul {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 80px;
        margin: auto;
        font-size: 0;
        height: 60px;
        text-align: center;
        position: absolute;
        padding: 0;
    }
    ul li {
        display: inline-block;
        width: 8px;
        height: 45px;
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -webkit-transform: translateY(0);
        transform: translateY(0);
        -moz-box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
        -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
        animation: colorbars 333ms alternate infinite cubic-bezier(0.2, 0, 0.5, 1);
    }

    .one {
        background: $color_loading1;
        border-radius: 3px 0 0 3px;
    }

    .two {
        background: $color_loading2;
    }

    .three {
        background: $color_loading3;
    }

    .four {
        background: $color_loading4;
    }

    .five {
        background: $color_loading5;
        border-radius: 0 3px 3px 0;
    }

    li:nth-of-type(1) {
        animation-delay: 111ms;
    }

    li:nth-of-type(2) {
        animation-delay: 222ms;
    }

    li:nth-of-type(3) {
        animation-delay: 333ms;
    }

    li:nth-of-type(4) {
        animation-delay: 444ms;
    }

    li:nth-of-type(5) {
        animation-delay: 555ms;
    }

    @keyframes colorbars {
        to {
            -moz-transform: translateY(12px);
            -ms-transform: translateY(12px);
            -webkit-transform: translateY(12px);
            transform: translateY(12px);
        }
    }
}
