.containerParticles{
	top: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	position: absolute;
	@media #{$media-tablet}{
		display: none;
	}
}

.mainContainer{
	// background-color: $negro-bg;
}

.fp-section{
	&:nth-of-type(1){
		overflow: hidden;
		background-color: $negro-bg;
		.container.home{
			border: none;
		}
	}
}

.container{
	width: 100%;
	height: 100%;
	max-height: 800px;
	max-width: 1366px;
	position: relative;
	background-color: rgba($blanco , 0.75);
	border: 1px solid $gris-menu;
	h1{
		font-size: 3rem;
		color: $negro-texto;
	}
}

.containerData{
	padding-left: 50px;
	padding-right: $tamanoHeader;
	height: calc(100% - #{$tamanoHeader});
	@include flexbox;
	@include flex-wrap(wrap);
	@media #{$media-tablet}{
		display: block;
		padding-left: 0;
		padding-right: 0;
		overflow-y: auto;
	}
	&-lr{
		width: 100%;
		@include flexbox;
		@include flex-wrap(wrap);
		height: calc(100% - (#{$tamanoFooter} + #{$tamanoReproductor}));
		@media #{$media-tablet}{
			height: auto;
			@include flex-wrap(wrap-reverse);
		}
	}
	&__left,
	&__right{
		height: 100%;
		@media #{$media-tablet}{
			height: auto;
			width: 100%;
			min-width: auto!important;
			max-width: none!important;
		}
	}
	&__left{
		min-width: 40%;
		max-width: 40%;
		overflow-y: auto;
		p.font-1{
			margin-top: 30px;
			padding-right: 30px;
			max-height: 205px;
			overflow-y: auto;
			@media #{$media-tablet}{
				margin-top: 20px;
				padding-right: 0;
				max-height: none;
				margin-bottom: 40px;
			}
		}
		@media #{$media-tablet}{
			padding: 30px 50px 0 50px;
		}
	}
	&__right{
		min-width: 60%;
		max-width: 60%;
		background-color: $negro-titulo;
		@media #{$media-tablet}{
			min-height: 240px;
		}
	}
}
