.modal{
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	z-index: 1100;
	position: fixed;
	max-width: calc(100% - 33px);
	@media #{$media-movil}{
		top: 5%;
		bottom: auto;
	}
	&--close{
		top: -33px;
		right: -33px;
		width: 66px;
		height: 66px;
		cursor: pointer;
		position: absolute;
		background-size: 100% 100%;
		background-repeat: no-repeat;
		background-image: url('../img/icons/iconClosemodal.png');
		@media #{$media-movil}{
			top: -15px;
			width: 33px;
			height: 33px;
			right: -15px;
		}
	}
	&__imagen{
		width: 80%;
		max-height: 85vh;
		text-align: center;
		&-img{
			img{
				max-height: 85vh;
			}
		}
		&-pie{
			display: none;
			color: $blanco;
			margin-top: 5px;
			@include roboto-regular;
		}
	}
	&__pdf{
		width: 80%;
		height: 85vh;
		&--pdf{
			border: 0;
		}
	}
	&__lyrics{
		width: 70%;
		height: 85vh;
		&_content{
			max-height: 100%;
			overflow-y: auto;
			text-align: center;
			color: $blanco;
		}
		&-img{}
		&-title{
			margin: 30px 0;
			h2{
				font-size: (25/16)+rem;
				@include roboto-bold;
			}
		}
		&-lyrics{
			p{
				line-height: 1.5;
				margin-bottom: 25px;
				font-size: (16/16)+rem;
				@include roboto-light;
			}
		}
	}
	&__youTube{
		width: 80%;
		height: 85vh;
		&_content{
			height: 100%;
			iframe{
				width: 100%;
				height: 100%;
			}
		}
	}
}
.bgModal{
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	position: fixed;
	cursor: pointer;
	background-color: rgba($negro-bg, 0.9);
}
