label.btn{
    input{
        display: none!important;
    }
}

.btn{
	border: none;
	outline: none;
	cursor: pointer;
	text-align: center;
	font-size: (16/16)+rem;
	@include roboto-medium;
	background-color: $blanco;
    @include transition(0.3);
	@include inline-flex;
	@include align-items(center);
	@include justify-content(center);
    &__enviar{
		color: $blanco;
		background-color: transparent;
        &:hover{
            // background-color: $blanco;
        }
    }
}

.animatedHover{
	overflow: hidden;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	&::before, &::after{
		content: "";
		bottom: 0;
		width: 50%;
		height: 2px;
		position: absolute;
		@include transition(0.3);
	}
	&::before{
		left: -50%;
	}
	&::after{
		right: -50%;
	}
	&:hover, &.active{
		&::before{
			left: 0;
		}
		&::after{
			right: 0;
		}
	}
	&--inverso{
		&::before{
			left: 0;
		}
		&::after{
			right: 0;
		}
		&:hover, &.active{
			&::before{
				left: -50%;
			}
			&::after{
				right: -50%;
			}
		}
	}
	&--verde{
		&::before, &::after{
			background-color: $verde-active;
		}
	}
}

.linkYoutube{
    a{
        display: block;
        &:hover{
            color: $negro;
        }
        &::before{
            content: "";
            width: 35px;
            height: 35px;
            margin-right: 5px;
            display: inline-block;
            vertical-align: middle;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            background-image: url('../img/player/play.png');
        }
    }
}
