body, html{
	margin: 0;
	padding: 0;
	font-size: 16px;
	font-family: 'roboto';
}

.hidden{
	display: none!important;
}

a{
	cursor: pointer;
	color: $negro-texto;
	&:link{
		color: $negro-texto;
	}
	&:active{
		color: darken($negro-texto, 10);
	}
	&:visited{
		color: $negro-texto;
	}
}

.no_events{
	pointer-events: none;
}
