// font-family: Roboto;

@mixin roboto-light {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

@mixin roboto-regular {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
}

@mixin roboto-medium {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
}

@mixin roboto-bold {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
}

@mixin roboto-black {
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
}

@mixin playfair {
    font-family: 'Playfair Display', serif;
	font-weight: 400;
}

.font__firstGreen{
	span{
		z-index: 1;
		position: relative;
		&::before{
			content: "";
			left: 0;
			bottom: 13px;
			height: 8px;
			width: 100%;
			display: block;
			position: absolute;
			background-color: $verde-active;
			z-index: -1;
			@media #{$media-movil}{
				height: 7px;
				bottom: 7px;
			}
		}
	}
}

.title1{
	@include roboto-regular;
	color: $negro-titulo;
	font-size: (70/16)+rem;
	@media #{$media-movil}{
		font-size: (40/16)+rem;
	}
}

.font-1{
	line-height: 28px;
	color: $negro-texto;
	font-size: (16/16)+rem;
	@include roboto-regular;
	span, b{
		@include roboto-bold;
	}
}
